<template>
    <div>
        <h1 class="text-center">Tempat Service Apapun Barang Elektronik anda</h1>
          <div class="col-12 d-flex p-2 justify-content-center">
            <div class="col-12 col-sm-12 col-md-12 col-lg-8 col-xl-8 mt-4 ">
                <button @click="showModal=true" class="btn btn-success mb-1">tambah</button>
                <!-- <a class="btn btn-warning mb-1" to="">Kembali</a> -->
                <router-link class="btn btn-warning mb-1" to="/" tag="button">Kembali</router-link>
                <div style="overflow-x: auto;">
                    <b-table-simple hover small caption-top responsive>
                        <caption>List servicenya gaes:</caption>
                        <colgroup><col><col></colgroup>
                        <colgroup><col><col><col></colgroup>
                        <colgroup><col><col></colgroup>
                        <b-thead head-variant="dark">
                        <b-tr>
                            <b-th>#</b-th>
                            <b-th>Nama Barang</b-th>
                            <b-th>Tingkat Kesulitan</b-th>
                            <b-th>Harga</b-th>
                            <b-th>Qty</b-th>
                            <b-th>Total Harga</b-th>
                            <b-th>Action</b-th>
                        </b-tr>
                        </b-thead>
                        <b-tbody>
                        <b-tr v-for="(row, index) in barang" :key="index">
                            <b-th scope="row">{{index+1}}</b-th>
                            <b-td>{{ row.nama }}</b-td>
                            <b-td>{{ difficulty.find(x => x.id == row.id_difficulty).nama}}</b-td>
                            <b-td>{{ difficulty.find(x => x.id == row.id_difficulty).harga}}</b-td>
                            <b-td>
                                <button type="button" class="btn btn-danger" v-on:click="countKurang(index)">-</button>
                                {{row.qty}}
                                <button type="button" class="btn btn-success" v-on:click="countTambah(index)">+</button>
                            </b-td>
                            <!-- disini kita mengalikan qty dengan harga pada id dificult -->
                            <!-- kalian hanya bisa log pada bagian javasctipt, bukan bagian dom ya :) -->
                            <b-td>{{ difficulty.find(x => x.id == row.id_difficulty).harga * row.qty}}</b-td>
                            <b-td>
                                <!-- buttonnya saya tambahin index untu edit dan hapus, anggep aja id nya kita ambil dan kita edit  -->
                                <button v-on:click="editModal(index)" class="btn btn-warning mr-1 ml-1">edit</button>
                                <button v-on:click="deleteData(index)" class="btn btn-danger mr-1 ml-1">delete</button>
                            </b-td>
                        </b-tr>
                        
                        </b-tbody>
                        <b-tfoot>
                        <!-- grandtotal akan tampil kalau barang ada valuenya, eh kok value. isinya maksutnya. -->
                          <b-tr v-if="barang.length > 0">
                              <b-td class="text-right" colspan="5">
                                <h4>grand Total</h4> 
                              </b-td>
                              <b-td>
                                  <!-- disini adalah script grand totalnya, sekali lagi ingat.  -->
                                  <!-- vue js hanya bisa di coding satu baris dan tidak bisa -->
                                  <!-- banyak baris sekaligus pada bagian domnya  -->
                                <h4> {{barang.reduce((acc, item) => acc + difficulty.find(x => x.id == item.id_difficulty).harga * item.qty, 0)}}</h4>
                              </b-td>
                              <b-td><button v-on:click="checkout" type="button" class="btn btn-danger"> Checkout </button></b-td>
                          </b-tr>
    
                          <!-- ini akan tampil kalau array barang masih belum mempunyai value -->
                          <b-tr v-if="!barang.length > 0">
                              <b-td class="text-center" colspan="7"> Belum ada data ya! silahkan tambahkan data terlebih dahulu!</b-td>
                          </b-tr>
                        </b-tfoot>
                    </b-table-simple>
                </div>
            </div>
          </div>
          <h1 class="text-center">test seberapa power fullnya vue js</h1>
            <b-modal
                v-model="showModal"
                title="Modal Variants"
            >   
                <!-- <template v-slot:modal-body> -->
                    <form>
                    <div class="form-group">
                      <label for="recipient-name" class="col-form-label">Nama Barang :</label>
                      <input type="text" class="form-control" v-model="dataModal.nama" id="recipient-name" v-bind:class="{ 'border': !dataModal.nama, 'border-danger': !dataModal.nama, 'border-3': !dataModal.nama }">
                      <div v-if="!dataModal.nama" style="display: block !important;" class="invalid-feedback">
                        Mohon Nama barang di isi!
                    </div>
                    </div>
                    <div class="form-group">
                        <label for="exampleFormControlSelect1">kesulitan</label>
                        <select class="form-control" v-bind:class="{ 'border': !dataModal.id_difficulty, 'border-danger': !dataModal.id_difficulty, 'border-3': !dataModal.id_difficulty }" v-model="dataModal.id_difficulty" id="exampleFormControlSelect1">
                          <option v-for="row in difficulty" :key="row.id" :value="row.id">{{row.nama}}</option>
                        </select>
                        <div v-if="!dataModal.id_difficulty" style="display: block !important;" class="invalid-feedback">
                            Mohon tingkat kesulitan di isi!
                        </div>
                      </div>
                    <div class="form-group">
                        <label for="recipient-name" class="col-form-label">Harga:</label>
                        <h6 v-if="dataModal.id_difficulty">{{ difficulty.find(x => x.id == dataModal.id_difficulty).harga}}</h6>
                        <h6 v-else>0</h6>
                    </div>
                    <div class="form-group">
                        <label for="recipient-name" class="col-form-label">QTY:</label>
                        <div class="row col-12">
                            <button type="button" class="btn btn-danger" v-on:click="countKurang">-</button>
                            <input style="width: min-content;" type="number" class="form-control" v-model="dataModal.qty" id="recipient-name" v-bind:class="{ 'border': !dataModal.qty, 'border-danger': !dataModal.qty, 'border-3': !dataModal.qty }">
                            <button type="button" class="btn btn-success" v-on:click="countTambah">+</button>
                        </div>
                        <div v-if="!dataModal.qty" style="display: block !important;" class="invalid-feedback">
                            Mohon QTY di isi!
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="recipient-name" class="col-form-label">Total Harga:</label>
                        <h1 v-if="!dataModal.qty">0</h1>
                        <h1 v-else-if="!dataModal.id_difficulty">0</h1>
                        <h1 v-else>{{ difficulty.find(x => x.id == dataModal.id_difficulty).harga * dataModal.qty}}</h1>
                    </div>
                  </form>
                <!-- </template> -->
                <template v-slot:modal-footer>
                    <div class="w-100">
                    <!-- <p class="float-left">Modal Footer Content</p> -->
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">batal</button>
                        <button v-if="isEdit" type="button" class="btn btn-warning" v-on:click="editData(dataModal.index)">Edit</button>
                        <button v-else type="button" class="btn btn-success" v-on:click="addData">tambah</button>
                    </div>
                </template>
            </b-modal>
    </div>
</template>

<script>
export default {
    data() {
      return {
            isEdit: false,
            showModal: false,
            difficulty: [
                {
                    id:1,
                    harga:2000,
                    nama: 'low',
                },
                {
                    id:2,
                    harga:3000,
                    nama: 'medium',
                },
                {
                    id:3,
                    harga:4000,
                    nama: 'high',
                },
            ],
            dataModal: {
                nama: null,
                id_difficulty: null,
                qty: null,
            },
            barang: [
                    {
                        nama: 'laptop',
                        id_difficulty: '1',
                        qty: '10',
                        
                    },
                    {
                        nama : 'laptop',
                        id_difficulty : '1',
                        qty : '10',
                        
                    }
            ]
      }
    },
    // santai aja fungsinya sama kok sama kayak javascript pada umumnya
    // kalian bisa maen lognya aja
    methods: {
        countTambah: function(index) {
            if (typeof index === "number") {
                this.barang[index].qty++
            } else {
                if (this.dataModal.qty == null) {
                    var data = this.dataModal
                    data["qty"] = 0
                    this.dataModal = data
                } else {
                    this.dataModal.qty++
                }
            }
        },
        countKurang: function(index) {
            if (typeof index === "number") {
                if (this.barang[index].qty > 1) {
                    this.barang[index].qty--
                } else {
                    this.$swal.fire({
                        title: 'Apa kamu yakin ?',
                        text: "Mengurangi QTY akan meremove item ini!",
                        icon: 'warning',
                        showCancelButton: true,
                        confirmButtonColor: '#3085d6',
                        cancelButtonColor: '#d33',
                        confirmButtonText: 'Oke!'
                        }).then((result) => {
                        if (result.value) {
                            this.barang.splice(index, 1)
                            this.$swal.fire(
                                'Terhapus!',
                                'Menghapus data sukses.',
                                'success'
                            )
                        }
                    })
                }
            } else {
                if (this.dataModal.qty == null) {
                    var data = this.dataModal
                    data["qty"] = 0
                    this.dataModal = data
                } else {
                    if (this.dataModal.qty > 0) {
                        this.dataModal.qty--
                    }
                }
            }
        },
        addData: function() {
            var data = this.dataModal
            var valid = 0
            $.each(data, function(row){
                if (!data[row]) {
                    valid++
                }
            })
            if (!valid > 0) {
                this.barang.push(this.dataModal)
                this.showModal = false
            } else {
                this.$swal.fire({
                    icon: 'error',
                    title: 'Lengkapi data anda!',
                    showConfirmButton: false,
                    timer: 1500
                })
            }
            
        },
        editData: function(index) {
            var data = this.dataModal
            var valid = 0
            $.each(data, function(row){
                if (row !== "index") {
                    if (!data[row]) {
                        valid++
                    }
                }
            })

            if (!valid > 0) {
                delete this.dataModal.index
                this.barang[index] = this.dataModal
                this.showModal = false
            } else {
                this.$swal.fire({
                    icon: 'error',
                    title: 'Lengkapi data anda!',
                    showConfirmButton: false,
                    timer: 1500
                })
            }
        },
        deleteData: function(index) {
            this.barang.splice(index, 1)
        },
        addModal: function() {
            this.dataModal = {
                nama: null,
                id_difficulty: null,
                qty: null,
            }
            this.isEdit = false
            this.showModal = true
        },
        editModal: function(index) {
            var data = this.barang[index]
            data["index"] = index
            this.dataModal = data
            this.isEdit = true
            this.showModal = true
        },
        checkout: function(params) {

            this.barang =[]
            this.$swal.fire({
                icon: 'success',
                title: 'Berhasil Checkout',
                showConfirmButton: false,
                timer: 2500
            })
        }
    },
}
</script>
<style scoped>

</style>


