import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/page/dasboard.vue'
import ServiceElectronic from '@/components/page/ServiceElectronic.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/serviceapps',
      name: 'Serciceapp',
      component: ServiceElectronic
    }
  ]
})



