<script>
import Navbar from './../Navbar'

export default {
  name: 'Dasboard',
  data() {
        return {
            portofolio: [
            {
              id:1,
              title: "DigiHotel",
              image: "Digihotel.png",
              desc: "Sebuah Aplikasi Management hotel. dengan aplikasi ini anda akan dimanjakan dengan beragam fitur menarik dan mempermudah management hotel anda.",
              link: "https://digihotel.id"
            },
            {
              id:2,
              title: "Dynamic",
              image: "Dynamic.png",
              desc: "Sebuah Aplikasi Internal dengan tujuan untuk mempermudah management rack penyewaan di datacenter milik Telkom Sigma.",
              link: ""
            },
            {
              id:3,
              title: "DBMS",
              image: "DBMS.png",
              desc: "Sebuah Aplikasi Third party milik CRM di Telkom Sigma, tujuan dibuat aplikasi ini karena CRM kekurangan beberapa field. sementara CRM diperbaiki, Telkom Sigma menggunakan aplikasi ini untuk input data pelanggan dan lainya.",
              link: ""
            },
          ]
        }
    },
    mounted(){
	// Activate scrollspy to add active class to navbar items on scroll
	(function($, window) {
		var adjustAnchor = function() {

			// The speed of the scroll in milliseconds
			const speed = 1000;
		
			// Find links that are #anchors and scroll to them
			$('a[href^="#"]')
			.not('.lp-pom-form .lp-pom-button')
			.unbind('click.smoothScroll')
			.bind('click.smoothScroll', function(event) {
				event.preventDefault();
				const href = $(this).attr('href').split('#');
				$('html, body').animate({ scrollTop: $(`#${href[1]}`).offset().top - 50 }, speed);
			});
		};

		$(window).on('hashchange load', function() {
			adjustAnchor();
		});

	})(jQuery, window);
	
	$(window).scroll(function() {
		var scrollDistance = $(window).scrollTop();

		if (scrollDistance > 100) {
		// 	$('.scroll-to-top').fadeIn();
		$("#mainNav").css({"padding-top": "1rem", "padding-bottom": "1rem"});
		} else {
		$("#mainNav").css({"padding-top": "", "padding-bottom": ""});
		}
		
		// Assign active class to nav links while scolling
		// console.log(scrollDistance);
			$('.page-section').each(function(i) {
					if ($(this).position().top - 70 <= scrollDistance) {
							if (i) {
								$('.js-scroll-trigger a.active').removeClass('active rounded');
								$('.js-scroll-trigger a').eq(i - 1).addClass('active rounded');	
							} else {
								$('.js-scroll-trigger a.active').removeClass('active rounded');
							}
					}
			});
	}).scroll();
  },
  components: {
    'navbar': Navbar,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



